import React from 'react';
import AuthProvider from './AuthComponents/AuthProvider';

const logFail = () => console.log("Login Failed")

const logSucess = () => console.log("Login Sucess");

const authConfig = {
    authUrl : "http://iam-dev.pearson.com/openam/oauth2/pearson/authorize",
    clientId : "sam_app",
    responseFormat : "response_type=id_token%20token&scope=openid&nonce=1234"
}

const Landing = (props) => (
    <div className="App">
        <AuthProvider loginSucess = {logSucess} loginFail={logFail}>
            <AuthProvider.LoggedIn>
                <h3>Logged In</h3>
            </AuthProvider.LoggedIn>
            <AuthProvider.LoggedOut>
                <h3>Logged Out</h3>
            </AuthProvider.LoggedOut>
            <AuthProvider.LoginButton variant="contained" color="primary"/>
            <h3><pre>{document.cookie}</pre></h3>
            <h3><pre>{document.cookie}</pre></h3>
        </AuthProvider>
    </div>
);
  
export default Landing;


