import React from 'react';
import { Button } from '@material-ui/core';

const AuthContext = React.createContext()

function AuthConsumer(props) {
    return (
      <AuthContext.Consumer {...props}>
        {context => {
          if (!context) {
            throw new Error(
              `Auth compound components cannot be rendered outside the Auth component`,
            )
          }
          return props.children(context)
        }}
      </AuthContext.Consumer>
    )
  }

class AuthProvider extends React.Component {
    static LoggedIn = ({children}) => (
        <AuthConsumer>
          {({loggedIn}) => (loggedIn ? children : null)}
        </AuthConsumer>
    )
    static LoggedOut = ({children}) => (
        <AuthConsumer>
          {({loggedIn}) => (loggedIn ? null : children)}
        </AuthConsumer>
    )
    static LoginButton = props => (
        <AuthConsumer>
          {({loggedIn, onInitLogin}) => (
            loggedIn ? null : <Button href="#" onClick={onInitLogin} {...props}>Login</Button>
          )}
        </AuthConsumer>
      )
    getParams = () => {
        let params = {};
        let vars = window.location.href.slice(window.location.href.indexOf('#') + 1).split('&');
        for (let i = 0; i < vars.length; i++) {
            let pair = vars[i].split('=');
            params[pair[0]] = decodeURIComponent(pair[1]);
        }
        return params;
    };

    componentDidMount() {
        const params = this.getParams();
        if(params.access_token){
            this.setState(
                {loggedIn: true},
                () => {
                  this.props.loginSucess(this.state.loggedIn)
                  window.localStorage.setItem('access_token',params.access_token);
                },
              )
        }
        else{
            this.setState(
                {loggedIn: false},
                () => this.props.loginFail(this.state.loggedIn),
              )
        }
    }

    nullFn = () => {};

    initLogin = () => {
        const authUrl = "http://iam-dev.pearson.com/openam/oauth2/pearson/authorize"
        const clientId = "sam_app";
        // const redirectUrl = "http://sam-bridge-dev.nonprod.pearsoncms.net/landing.html";
        const redirectUrl = window.location.href;
        const responseFormat = "response_type=id_token%20token&scope=openid&nonce=1234";
        // const redirectUrl = `http://iam-dev.pearson.com/openam/oauth2/pearson/authorize?response_type=id_token%20token&scope=openid&nonce=1234&client_id=sam_app&redirect_uri=http://sam-bridge-dev.nonprod.pearsoncms.net/landing.html`
        const authTokenUrl = `${authUrl}?${responseFormat}&client_id=${clientId}&redirect_uri=${redirectUrl}`;
        window.location = authTokenUrl;
    }
    
    static defaultAuthParams = {
      // authUrl : "http://iam-dev.pearson.com/openam/oauth2/pearson/authorize",
      // clientId : "sam_app",
      // responseFormat : "response_type=id_token%20token&scope=openid&nonce=1234",
      redirectUrl : window.location.href
    }
    
    state = {loggedIn: true, authParams : {},
        onInitLogin : this.initLogin,
        onLoginSuccess : this.nullFn,onLoginFail : this.nullFn};

    render() {
        return (
          <AuthContext.Provider value={this.state}>
            {this.props.children}
          </AuthContext.Provider>
        )
      }

}


export default AuthProvider;


